.card{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 15rem;
    margin: 10px;
    padding: 5px;
    border-radius: 10px;
    box-shadow:  7px 7px 5px 0px rgba(50, 50, 50, 0.75);
}

img{
    width: 5rem;
    margin-right: 10px;
    filter: drop-shadow(3px 5px 2px rgb(0 0 0 / 0.5));
}